import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators,FormControl } from '@angular/forms';
import { AuthService } from 'src/app/servicios/auth.service';
import { Login } from 'src/app/clases/Login';
import { Router } from '@angular/router';

@Component({
  selector: 'app-empleados-login',
  templateUrl: './empleados-login.component.html',
  styleUrls: ['./empleados-login.component.scss']
})
export class EmpleadosLoginComponent implements OnInit {

  public form: FormGroup;
  public error: boolean;
  public errorMessage: string;

  validation_messages = {
    'mail': [
      { type: 'required', message: 'Debe ingresar un email.' },
      { type: 'email', message: 'Debe ingresar un email válido.' }
    ],
    'password': [
      { type: 'required', message: 'Debe ingresar una contraseña.' }
    ]
  };

  constructor(private fb: FormBuilder, private authService: AuthService, private router: Router) {
   
   /*  this.form = this.fb.group({
      user: new FormControl('', Validators.compose([
        Validators.required,
        Validators.user
      ])),
      password: new FormControl('', Validators.required)
    }); */
   
   
   this.form = this.fb.group({
      user: ['', Validators.required],
      pass: ['', Validators.required]
    });
 
  }

  ngOnInit() {
  }

  CargarDefault(tipo: string) {
    let dataLogin: Login = null;
    switch (tipo) {
      case 'S':
        dataLogin = new Login('admin', 'admin');
        this.form.setValue(dataLogin);
        this.Submit();
        break;
      case 'B':
        dataLogin = new Login('Matias', '1234');
        this.form.setValue(dataLogin);
        this.Submit();
        break;
      case 'CE':
        dataLogin = new Login('cervecero', 'cervecero');
        this.form.setValue(dataLogin);
        this.Submit();
        break;
      case 'CO':
        dataLogin = new Login('cocinero', 'cocinero');
        this.form.setValue(dataLogin);
        this.Submit();
        break;
      case 'M':
        dataLogin = new Login('mozo', 'mozo');
        this.form.setValue(dataLogin);
        this.Submit();
        break;
    }
  }

  public Submit(): void {
    this.errorMessage = '';
    this.error = false;
    if (this.form.valid) {
      const dataLogin: Login = new Login(this.form.get('user').value,
        this.form.get('pass').value);
      this.authService.Loguear(dataLogin)
        .then(
          res => {
            //if (response['Estado'] === 'OK') {
            //  localStorage.setItem('token', response['Token']);
              if (!this.authService.redirectUrl) {
                this.authService.redirectUrl = '/Empleados';
              }
              this.router.navigate([this.authService.redirectUrl]);
           // } else {
           //   this.error = true;
           //   this.errorMessage = response['Mensaje'];
           // }
          }
        )
        .catch(error => {
            this.error = true;
            //this.errorMessage = res['Mensaje'];
            if (error.code === 'auth/user-not-found') {
              //this.toastService.error('Usuario no encontrado.');
            this.errorMessage='Usuario no encontrado.';
            } else if (error.code === 'auth/wrong-password') {
              //this.toastService.error('Contraseña incorrecta.');
            this.errorMessage='Contraseña incorrecta.';
            } else {
              //this.toastService.error('Ocurrió un error, contáctese con el administrador.');
            this.errorMessage='Ocurrió un error, contáctese con el administrador.';
            }     
          }
        );
    } else {
      this.errorMessage = 'Debe completar los campos correctamente.';
      this.error = true;
    }
  }




}
